/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

body {
  font-size: 1.6rem;
  line-height: 1.7;
}

h1,
h2,
h3 {
  line-height: 1;
  margin-bottom: 3rem;
  font-family: "Roboto", sans-serif;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  line-height: 1;
  margin-bottom: 3rem;
  font-size: 2.2em;
}

strong {
  font-weight: bold;
}

.text-small {
  font-size: 1rem;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  overflow-x: hidden;
  min-height: 100%;
  color: black;
}

p {
  margin-bottom: 1.5rem;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

ol {
  display: block;
  list-style-type: decimal;
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
  padding-left: 40px;
  overflow: hidden;
}

ul {
  list-style-type: circle;
  overflow: hidden;
}

li {
  margin-left: 3.5rem;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table, th, td {
  border: 1px solid black;
}

th, td {
  padding: 1rem;
}

.container {
  width: 120rem;
  margin: 0 auto;
  border: 2px solid black;
  height: 100vh;
  padding: 2rem;
}

.card {
  background-color: #00c800;
  color: black;
  padding: 2rem 3rem;
}
